(() => {
  "use strict";

  // –ü–æ–ª—É—á–∞–µ–º –≤—Å–µ —Ñ–æ—Ä–º—ã, –∫ –∫–æ—Ç–æ—Ä—ã–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø—Ä–∏–º–µ–Ω–∏—Ç—å –∫–∞—Å—Ç–æ–º–Ω—ã–µ —Å—Ç–∏–ª–∏ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ Bootstrap
  const forms = document.querySelectorAll(".needs-validation");

  // –ü—Ä–æ—Ö–æ–¥–∏–º –ø–æ –≤—Å–µ–º —Ñ–æ—Ä–º–∞–º –∏ –¥–æ–±–∞–≤–ª—è–µ–º —Å–æ–±—ã—Ç–∏–µ –æ—Ç–ø—Ä–∞–≤–∫–∏
  Array.from(forms).forEach((form) => {
    form.addEventListener(
      "submit",
      async (event) => {
        // –ï—Å–ª–∏ —Ñ–æ—Ä–º–∞ –Ω–µ –≤–∞–ª–∏–¥–Ω–∞, –ø—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ–º –æ—Ç–ø—Ä–∞–≤–∫—É
        if (!form.checkValidity()) {
          event.preventDefault();
          event.stopPropagation();
        } else {
          // –ï—Å–ª–∏ —Ñ–æ—Ä–º–∞ –≤–∞–ª–∏–¥–Ω–∞, –≤—ã–ø–æ–ª–Ω—è–µ–º –æ—Ç–ø—Ä–∞–≤–∫—É —á–µ—Ä–µ–∑ API
          event.preventDefault(); // –û—Ç–º–µ–Ω—è–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–µ –ø–æ–≤–µ–¥–µ–Ω–∏–µ –æ—Ç–ø—Ä–∞–≤–∫–∏

          // –°–æ–±–∏—Ä–∞–µ–º –¥–∞–Ω–Ω—ã–µ —Ñ–æ—Ä–º—ã
          const formData = {
            startup_name: document.getElementById("StartupName").value.trim(),
            team_name: document.getElementById("TeamName").value.trim(),
            theme_id: parseInt(document.getElementById("StartupTheme").value), // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –≤ —á–∏—Å–ª–æ
            category_id: parseInt(
              document.getElementById("StartupCathegory").value,
            ), // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –≤ —á–∏—Å–ª–æ
            description: document.getElementById("Description").value.trim(),
            start_m: parseInt(document.getElementById("Start_m").value),
            investments_m: parseInt(
              document.getElementById("Investments_m").value,
            ),
            crowdfunding_m: parseFloat(
              document.getElementById("Crowdfunding_m").value,
            ),
            team_size: parseInt(document.getElementById("TeamCount").value),
            team_index: parseInt(document.getElementById("TeamIndex").value),
            team_mapping: document.getElementById("TeamExp").value,
            tech_level: document.getElementById("Techonological_kef").value,
            tech_investment: parseInt(
              document.getElementById("Techological_m").value,
            ),
            competition_level: document.getElementById("Competition").value,
            competitor_count: parseInt(
              document.getElementById("CompetititorCount").value,
            ),
            demand_level: document.getElementById("DemandLevel").value,
            social_impact: document.getElementById("DemandIdx").value,
            audience_reach: parseInt(
              document.getElementById("TargetAudience").value,
            ),
            market_size: parseInt(document.getElementById("MarketSize").value),
          };

          console.log("–û—Ç–ø—Ä–∞–≤–ª—è–µ–º—ã–µ –¥–∞–Ω–Ω—ã–µ:", formData);
          console.log(
            "–û—Ç–ø—Ä–∞–≤–ª—è–µ–º—ã–µ –¥–∞–Ω–Ω—ã–µ:",
            JSON.stringify(formData, null, 2),
          );

          try {
            // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –¥–∞–Ω–Ω—ã–µ –Ω–∞ API
            const response = await fetch(
              "http://127.0.0.1:8001/predict/full_form",
              {
                method: "POST",
                headers: {
                  "Content-Type": "application/json",
                },
                body: JSON.stringify(formData),
              },
            );

            if (!response.ok) {
              throw new Error(`–û—à–∏–±–∫–∞: ${response.statusText}`);
            }

            const result = await response.json();
            console.log("Prediction:", result.prediction);
            console.log("calculate_indices:", result.calculate_indices);
            alert(
              `–†–µ–∑—É–ª—å—Ç–∞—Ç –ø—Ä–µ–¥—Å–∫–∞–∑–∞–Ω–∏—è: ${JSON.stringify(result.prediction)}`,
            );
          } catch (error) {
            console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ –∑–∞–ø—Ä–æ—Å–∞:", error);
            alert(
              "–ù–µ —É–¥–∞–ª–æ—Å—å –≤—ã–ø–æ–ª–Ω–∏—Ç—å –ø—Ä–µ–¥—Å–∫–∞–∑–∞–Ω–∏–µ. –ü—Ä–æ–≤–µ—Ä—å—Ç–µ –∫–æ–Ω—Å–æ–ª—å –¥–ª—è –ø–æ–¥—Ä–æ–±–Ω–æ—Å—Ç–µ–π.",
            );
          }
        }

        form.classList.add("was-validated"); // –î–æ–±–∞–≤–ª—è–µ–º Bootstrap-—Å—Ç–∏–ª—å –≤–∞–ª–∏–¥–∞—Ü–∏–∏
      },
      false,
    );
  });
})();
